import React, { useState, useEffect } from 'react';
import './Settings.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFolder, faPalette, faLanguage, faServer, faShield, faInfoCircle, faCode } from '@fortawesome/free-solid-svg-icons';
import { useUser } from '../../context/UserContext';

function Settings() {
  const [activeTab, setActiveTab] = useState('general');
  const [appVersion, setAppVersion] = useState('');
  const { isBlocked, blockUser, unblockUser, username, resetUsername } = useUser();
  
  useEffect(() => {
    if (window.electron && window.electron.ipcRenderer) {
      window.electron.ipcRenderer.once('app-version', (version) => {
        setAppVersion(version);
      });
      window.electron.ipcRenderer.sendMessage('get-app-version');
    } else {
      setAppVersion('0.0.1');
    }
  }, []);
  {/*{ id: 'security', name: '', icon: faShield } */}
  const tabs = [
    { id: 'general', name: '', icon: faFolder },
    { id: 'appearance', name: ' ', icon: faPalette },
    { id: 'network', name: '', icon: faServer },

    { id: 'about', name: ' ', icon: faInfoCircle },
    { id: 'developer', name: ' ', icon: faCode }
  ];
  
  const toggleBlockStatus = () => {
    if (isBlocked) {
      unblockUser();
    } else {
      blockUser();
    }
  };

  const handleForceLogout = () => {
    resetUsername();
  };
  


  const openDevTools = () => {
    if (window.electron && window.electron.ipcRenderer) {
      window.electron.ipcRenderer.sendMessage('open-devtools');
    } else {
      try {
        console.log(' DevTools   API ');
        if (window.devtoolsOpen) {
          window.devtoolsOpen();
        } else {
          alert(' F12    ');
        }
      } catch (error) {
        console.error('   DevTools:', error);
      }
    }
  };
  
  const renderTabContent = () => {
    switch (activeTab) {
      case 'general':
        return (
          <div className="settings-section">
            <h3> </h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4>  Minecraft</h4>
                <p>     </p>
              </div>
              <div className="setting-control">
                <input type="text" className="path-input" value="C:\Users\Username\AppData\Roaming\.minecraft" readOnly />
                <button className="browse-button"></button>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4></h4>
                <p>    Windows</p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
            </div>
        );
      case 'appearance':
        return (
          <div className="settings-section">
            <h3> </h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4></h4>
                <p>  </p>
              </div>
              <div className="setting-control">
                <select className="theme-select">
                  <option value="light"></option>
                  <option value="dark"></option>
                  <option value="system"></option>
                </select>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4> </h4>
                <p>    </p>
              </div>
              <div className="setting-control">
                <select className="font-size-select">
                  <option value="small"></option>
                  <option value="medium" selected></option>
                  <option value="large"></option>
                </select>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4> </h4>
                <p>   </p>
              </div>  
              <div className="setting-control">
                <button className="background-button"> </button>
              </div>
            </div>
          </div>
        );
      case 'network':
        return (
          <div className="settings-section">
            <h3></h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4> </h4>
                <p>   . (    )</p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
          </div>
        );
      case 'security':
        return (
          <div className="settings-section">
            <h3></h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4> </h4>
                <p>   </p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4> </h4>
                <p>    </p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" defaultChecked />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
          </div>
        );
      case 'developer':
        return (
          <div className="settings-section">
            <h3> </h3>
            <div className="dev-warning">
              <FontAwesomeIcon icon={faCode} className="warning-icon" />
              <p>  , </p>
            </div>
            
            {username && (
              <>
                <div className="setting-item">
                  <div className="setting-info">
                    <h4> </h4>
                    <p>   </p>
                  </div>
                  <div className="setting-control">
                    <button 
                      className={`block-toggle-button ${isBlocked ? 'blocked' : 'active'}`}
                      onClick={toggleBlockStatus}
                    >
                      {isBlocked ? '' : ''}
                    </button>
                  </div>
                </div>
                
                <div className="setting-item">
                  <div className="setting-info">
                    <h4> </h4>
                    <p>       </p>
                  </div>
                  <div className="setting-control">
                    <button 
                      className="dev-button"
                      onClick={handleForceLogout}
                    >
                       
                    </button>
                  </div>
                </div>
              </>
            )}
            
            <div className="setting-item">
              <div className="setting-info">
                <h4> </h4>
                <p>  </p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
          </div>
        );
      case 'about':
        return (
          <div className="settings-section">
            <h3> </h3>
            <div className="about-info">
              <div className="app-logo">
                <img src="https://via.placeholder.com/100" alt="CMLauncher" />
              </div>
              <div className="app-details">
                <h2>CMLauncher</h2>
                <p className="version"> {appVersion}</p>
                <p className="description">
                  CMLauncher -    Minecraft   ,
                     .
                </p>
                <div className="app-links">
                  <a href="https://cmlauncher.ru" className="app-link" target="_blank" rel="noopener noreferrer"> </a>
                </div>
                <div className="creator-info">
                  <img src="https://via.placeholder.com/40" alt="creator" className="creator-avatar" />
                  <div className="creator-details">
                    <p className="creator">Made by Revellison</p>
                    <a href="https://revellison.ru" className="app-link" target="_blank" rel="noopener noreferrer">revellison.ru</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
      default:
        return null;
    }
  };
  
  return (
    <div className="settings-page">
      <div className="settings-sidebar">
        <h2></h2>
        <div className="settings-tabs">
          {tabs.map(tab => (
            <button 
              key={tab.id}
              className={`settings-tab ${activeTab === tab.id ? 'active' : ''}`}
              onClick={() => setActiveTab(tab.id)}
              data-tab={tab.id}
            >
              <FontAwesomeIcon icon={tab.icon} />
              <span>{tab.name}</span>
            </button>
          ))}
        </div>
      </div>
      <div className="settings-content">
        {renderTabContent()}
      </div>
    </div>
  );
}

export default Settings;